import React from 'react'

import { Video } from 'react-video-stream'
const url = 'http://example.com/manifest.mpd'

const options: any = {
  requestHeader: 'Authorization',
  requestToken: 'access_token'
}
const App = () => {
  return (
    <div>
      <Video
        className='video-class'
        controls={true}
        autoPlay={true}
        options={options}
        remoteUrl={url}
      />
    </div>
  )
}

export default App
